.resume-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  padding-top: 2%;

}

.contact-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 60vh;
  width: 100vw;
}

.contact-wrap p {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin: 1em;
  padding:1em;
  &:hover {
    cursor: pointer;
    background-color: rgba(34, 34, 34, 0.178);
    transition: all 0.25s ease-in-out;
  }
}

.contact-wrap p span {
display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  width:4em;
}
.contact-wrap a {
  text-decoration: none;
  color: #222;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  margin: 0 2rem;
  font-weight: bold;
      font-family: 'Special Elite', cursive;


}
