@mixin tab($col, $tabPos, $label) {

  &::after {
    @extend .menu-font;
    @extend .menu-size;
    content: $label;
    position: absolute;

    background: $col;
    // border-radius: 20px 20px 20px 0;
    left: $tabPos;

  }
}

$col1: #F3DBB2;
$col2: #CFB2B0;
$col3: #FCC0C5;
$col4: #B7C8C2;
$col5: #CFB9B0;
$col6: #E7DED9;
$col7: #B7CCC2;

* {

  line-height: 1.7em;
}

html,
body {
  width: 100vw;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  height: 100vh;
  padding: 0;
  background: rgb(51, 51, 51);
}

.tab {
  width: 100%;
  position: absolute;
  box-shadow: 6px 0px 6px rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease;
  &:hover~div {
    transform: translateY(80vh);
  }
}



#tab0 {
  @extend .tab;

  height: 100vh;
  padding: 0;
  background: rgb(51, 51, 51);

}

#tab7 {
  @extend .tab;
  @extend .tab-class;
  background: $col7;
  @include tab($col7, 72%, 'Resume');
}

#tab6 {
  @extend .tab;
  @extend .tab-class;
  background: $col6;
  @include tab($col6, 60%, 'Resume');
}

#tab5 {
  @extend .tab;
  @extend .tab-class;
  background: $col5;
  @include tab($col5, 48%, 'Contact');
}

#tab4 {
  @extend .tab;
  @extend .tab-class;
  background: $col4;
  @include tab($col4, 36%, 'Skills');
}

#tab3 {
  @extend .tab;
  @extend .tab-class;
  background: $col3;
  @include tab($col3, 24%, 'Projects');
}

#tab2 {
  @extend .tab;
  @extend .tab-class;
  background: $col2;
  @include tab($col2, 12%, 'Experiance');
}

#tab1 {
  @extend .tab;
  @extend .tab-class;
  background: $col1;
  @include tab($col1, 0, 'About');

}

